:root {
  --accecent-color: #00acdc;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body {
  margin: 0;
}

.header {
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

.logo {
  align-self: center;
  height: 40px;
}

.nav {
  align-self: center;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav__item {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  margin: 0 10px;
  display: flex;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.is-active {
  color: var(--accecent-color);
  position: relative;
}

.is-active:after {
  content: "";
  border-bottom: 4px solid var(--accecent-color);
  border-radius: 8px;
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link:hover {
  color: var(--accecent-color);
}
